import React, { useState, useContext, useEffect } from 'react';
import { firebase } from '../firebase';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../AuthProvider';
import '../styles/UserSetup.css';

const UserSetupPage = () => {
  const [userName, setUserName] = useState('');
  const [userTeam, setUserTeam] = useState('');
  const [isRole, setIsRole] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const navigate = useNavigate();
  const { currentUser, isAdmin } = useContext(AuthContext);

  useEffect(() => {
    if (!currentUser) {
      navigate('/login');
      return;
    }

    //    
    const loadUserInfo = async () => {
      try {
        const userDoc = await firebase.firestore()
          .collection("Users")
          .doc(currentUser.uid)
          .get();

        if (userDoc.exists) {
          const userData = userDoc.data();
          setUserName(userData.UserName || '');
          setUserTeam(userData.UserTeam || '');
          setIsRole(userData.isRole || '');
        }
        setIsLoading(false);
      } catch (error) {
        console.error("Error loading user info:", error);
        setIsLoading(false);
      }
    };

    loadUserInfo();
  }, [currentUser, navigate]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      await firebase.firestore().collection("Users").doc(currentUser.uid).update({
        UserName: userName,
        UserTeam: userTeam,
        isRole: isRole,
      });
      
      //     
      navigate(isAdmin ? '/admindash' : '/dashboard');
    } catch (error) {
      console.error("Error updating user info:", error);
    }
  };

  if (isLoading) {
    return <div className="user-setup-container">Loading...</div>;
  }

  return (
    <div className="user-setup-container">
      <div className="user-setup-form">
        <h2>  </h2>
        <p>    .</p>
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label>:</label>
            <input
              type="text"
              value={userName}
              onChange={(e) => setUserName(e.target.value)}
              required
              placeholder=" "
            />
          </div>
          <div className="form-group">
            <label>:</label>
            <input
              type="text"
              value={userTeam}
              onChange={(e) => setUserTeam(e.target.value)}
              required
              placeholder="  "
            />
          </div>
          <div className="form-group">
            <label>:</label>
            <select
              value={isRole}
              onChange={(e) => setIsRole(e.target.value)}
              required
            >
              <option value=""></option>
              <option value="1"></option>
              <option value="2"></option>
              <option value="3"></option>
            </select>
          </div>
          <button type="submit" className="submit-button"> </button>
        </form>
      </div>
    </div>
  );
};

export default UserSetupPage; 