import React, { useRef,useState, useEffect } from 'react';
import Select from 'react-select';
import { db } from '../../firebase'; // Firestore  
import firebase from 'firebase/app';
import { debounce } from 'lodash'; // lodash   241217
import Calendar from 'react-calendar'; // react-calendar import
import 'react-calendar/dist/Calendar.css'; // react-calendar css import
import './myproject.css';

function MyProject() {
  const [tasks, setTasks] = useState([]);
  const [projects, setProjects] = useState([]);
  const [departmentData, setDepartmentData] = useState({});
  const [currentUserId, setCurrentUserId] = useState(null);
  const [userData, setUserData] = useState(null); //   

  const [filters, setFilters] = useState({
    year: new Date().getFullYear(),
    month: new Date().getMonth() + 1,
  });

  // Calendar 
  const [selectedTaskId, setSelectedTaskId] = useState(null);
  const [isCalendarOpen, setIsCalendarOpen] = useState(false);
  const [calendarPosition, setCalendarPosition] = useState({ top: 0, left: 0 });

  //   
  const depList = [
    { value: 'business', label: '' },
    { value: 'Management', label: '' },
    { value: 'Design', label: '' },
    { value: 'newLeaf', label: '' }
  ];

  //  
  const [selectedDepartment, setSelectedDepartment] = useState('');
  const [showDepartmentSelect, setShowDepartmentSelect] = useState(false);

  useEffect(() => {
    const user = firebase.auth().currentUser;
    if (user) {
      setCurrentUserId(user.uid);
    }
  }, []);

  //    
  useEffect(() => {
    if (currentUserId) {
      db.collection('Users')
        .doc(currentUserId)
        .get()
        .then((doc) => {
          if (doc.exists) {
            setUserData(doc.data());
          } else {
            console.error('    .');
          }
        })
        .catch((error) => console.error('Error fetching user data:', error));
    }
  }, [currentUserId]);

  useEffect(() => {
    if (currentUserId) {
      //   YYYY-MM-DD  
      const startOfMonth = new Date(filters.year, filters.month - 1, 1)
        .toISOString()
        .split('T')[0];
      const endOfMonth = new Date(filters.year, filters.month, 0)
        .toISOString()
        .split('T')[0];

      db.collection('tasks')
        .where('Uesrsid', '==', currentUserId)
        .where('date', '>=', startOfMonth)
        .where('date', '<=', endOfMonth)
        .get()
        .then((snapshot) => {
          const fetchedTasks = snapshot.docs.map((doc) => {
            const taskData = doc.data();
            const category = departmentData.categories.find(
              (cat) => cat.name === taskData.categoriesName
            );
            const subcategory = category
              ? category.subcategories.find(
                  (sub) => sub.name === taskData.subcategoriesName
                )
              : null;

            return {
              id: doc.id,
              ...taskData,
              availableSubcategories: category ? category.subcategories : [],
              availableSubSubcategories: subcategory
                ? subcategory.subsubcategories
                : [],
              userName: userData.UserName || '  ',
              userTeam: userData.UserTeam || '  ',
            };
          });
          setTasks(fetchedTasks);
        })
        .catch((error) => console.error('Error fetching tasks:', error));
    }
  }, [currentUserId, departmentData, filters, userData]);
  
  
  useEffect(() => {
    //     
    const departments = ['business', 'Management', 'Design'];
    
    departments.forEach(documentName => {
      db.collection('departments')
        .doc(documentName)
        .get()
        .then((doc) => {
          if (doc.exists) {
            setDepartmentData(prev => ({
              ...prev,
              [documentName]: doc.data()
            }));
          } else {
            console.error(`No such document: ${documentName}`);
          }
        })
        .catch((error) => console.error('Error fetching department data:', error));
    });
  }, []);

  useEffect(() => {
    db.collection('projects')
      .get()
      .then((snapshot) => {
        const fetchedProjects = snapshot.docs.map((doc) => ({
          id: doc.id,
          name: doc.data().name,
        }));
        // React-Select options  
        const formattedOptions = fetchedProjects.map((project) => ({
          value: project.name, // value 
          label: project.name, // label  (  )
        }));
        setProjects(formattedOptions);
      })
      .catch((error) => console.error('Error fetching projects:', error));
  }, []);

    //  useEffect 
    useEffect(() => {
      tasks.forEach((task) => {
        const baseTime = task.baseTime || 0;
        const spentTime = parseFloat(task.spentTime) || 0;
        const remainingTime = baseTime - spentTime;
  
        //      
        const updatedStatus =
        task.status === ''
          ? ''
          : remainingTime < 0
          ? ''
          : task.status; //   
  
        // Firebase 
        if (task.remainingTime !== remainingTime || task.status !== updatedStatus) {
          db.collection('tasks')
            .doc(task.id)
            .update({ remainingTime, status: updatedStatus })
            .then(() =>
              console.log(`Updated task: ${task.id}, Remaining Time: ${remainingTime}, Status: ${updatedStatus}`)
            )
            .catch((error) => console.error('Error updating task:', error));
        }
      });
    }, [tasks]);

    // Debounced        
    const debouncedUpdateRef = useRef(
    debounce((taskId, updateData) => {
      db.collection('tasks')
        .doc(taskId)
        .update(updateData)
        .then(() => console.log(`Task ${taskId} updated`))
        .catch((error) => console.error('Error updating task:', error));
    }, 500) // 0.5  
    );

    const handleFilterChange = (field, value) => {
      setFilters((prev) => ({ ...prev, [field]: value }));
    };

    const updateTaskField = async (taskId, field, value) => {
      const currentTask = tasks.find((task) => task.id === taskId);
      
      //       
      if (field === 'department') {
        await fetchDepartmentData(value);
      }

      //      
      if (!departmentData[currentTask.department]) {
        await fetchDepartmentData(currentTask.department);
      }

      //    
      if (field === 'categoriesName') {
        //      
        if (currentTask.subcategoriesName || currentTask.subsubcategoriesName) {
          if (window.confirm('     . ?')) {
            const selectedCategory = departmentData[currentTask.department].categories.find(
              (category) => category.name === value
            );

            const updatedTask = {
              categoriesName: value,
              subcategoriesName: '',
              subsubcategoriesName: '',
              availableSubcategories: selectedCategory.subcategories || [],
              availableSubSubcategories: [],
              baseTime: 0 //  
            };

            //  Firestore 
            db.collection('tasks')
              .doc(taskId)
              .update(updatedTask)
              .then(() => {
                // Firestore      
                setTasks((prevTasks) =>
                  prevTasks.map((task) =>
                    task.id === taskId
                      ? {
                          ...task,
                          ...updatedTask
                        }
                      : task
                  )
                );
              })
              .catch((error) => {
                console.error('Error updating task:', error);
                alert('    .');
              });
          }
          return; //       
        }
      }

      if (field === 'subcategoriesName') {
        //     
        if (currentTask.subsubcategoriesName) {
          if (window.confirm('    . ?')) {
            const selectedSubcategory = currentTask.availableSubcategories.find(
              (subcategory) => subcategory.name === value
            );

            const updatedTask = {
              subcategoriesName: value,
              subsubcategoriesName: '',
              availableSubSubcategories: selectedSubcategory.subsubcategories || [],
              baseTime: 0 //  
            };

            //  Firestore 
            db.collection('tasks')
              .doc(taskId)
              .update(updatedTask)
              .then(() => {
                // Firestore      
                setTasks((prevTasks) =>
                  prevTasks.map((task) =>
                    task.id === taskId
                      ? {
                          ...task,
                          ...updatedTask
                        }
                      : task
                  )
                );
              })
              .catch((error) => {
                console.error('Error updating task:', error);
                alert('    .');
              });
          }
          return; //       
        }
      }
    
      // spentTime       
      if (field === 'spentTime') {
        const baseTime = currentTask.baseTime || 0;
        const spentTime = Math.max(0, parseFloat(value)) || 0;
        const remainingTime = baseTime - spentTime;

        //       
        if (remainingTime < 0 && currentTask.status !== '') {
          if (window.confirm(' .   ?')) {
            //   
            setTasks((prevTasks) =>
              prevTasks.map((task) =>
                task.id === taskId
                  ? {
                      ...task,
                      spentTime: value,
                      remainingTime,
                      status: '',
                    }
                  : task
              )
            );

            // Firestore 
            db.collection('tasks')
              .doc(taskId)
              .update({
                spentTime: value,
                remainingTime,
                status: '',
              });
          } else {
            //    spentTime   
            const previousSpentTime = currentTask.spentTime;
            setTasks((prevTasks) =>
              prevTasks.map((task) =>
                task.id === taskId
                  ? {
                      ...task,
                      spentTime: previousSpentTime,
                      remainingTime: baseTime - previousSpentTime,
                    }
                  : task
              )
            );
          }
          return; //  
        }
      }
    
      //  ''  , note()  
      if (field === 'status' && value === '' && !currentTask.note.trim()) {
        alert('   ""   .');
        return;
      }
    
      // ''       note() 
      if (currentTask.status === '' && field === 'status' && value !== '') {
        if (!currentTask.note.trim()) {
          alert('  ""      .');
          return;
        }
      }
    
      // ''       note()  
      if (currentTask.status === '' && field !== 'note' && !currentTask.note.trim()) {
        alert('      .');
        return;
      }
    
      setTasks((prevTasks) =>
        prevTasks.map((task) =>
          task.id === taskId
            ? {
                ...task,
                [field]: value,
                ...(field === 'categoriesName' && {
                  subcategoriesName: '',
                  subsubcategoriesName: '',
                  availableSubcategories:
                    departmentData[task.department].categories.find((category) => category.name === value)
                      .subcategories || [],
                  availableSubSubcategories: [],
                }),
                ...(field === 'subcategoriesName' && {
                  subsubcategoriesName: '',
                  availableSubSubcategories:
                    currentTask.availableSubcategories.find(
                      (subcategory) => subcategory.name === value
                    ).subsubcategories || [],
                }),
                ...(field === 'spentTime' && {
                  remainingTime: (() => {
                    const baseTime = currentTask.baseTime || 0;
                    const spentTime = Math.max(0, parseFloat(value)) || 0;
                    return baseTime - spentTime;
                  })(),
                  status: (() => {
                    const baseTime = currentTask.baseTime || 0;
                    const spentTime = Math.max(0, parseFloat(value)) || 0;
                    const remainingTime = baseTime - spentTime;
    
                    if (remainingTime < 0) return '';
                    if (task.status === '') return '';
                    if (task.status === '') return '';
                    return '';
                  })(),
                }),
              }
            : task
        )
      );
    
      // Firestore  (note   )
      if (field === 'note') {
        debouncedUpdateRef.current(taskId, { [field]: value });
      } else {
        const baseTime = currentTask.baseTime || 0;
        const spentTime =
          field === 'spentTime' ? Math.max(0, parseFloat(value)) || 0 : currentTask.spentTime || 0;
        const remainingTime = baseTime - spentTime;
    
        const updateData = {
          [field]: value,
          ...(field === 'spentTime' && {
            remainingTime,
            status:
              remainingTime < 0
                ? ''
                : currentTask.status === ''
                ? ''
                : '',
          }),
        };
    
        db.collection('tasks')
          .doc(taskId)
          .update(updateData)
          .then(() => console.log(`Task ${taskId} updated: ${field} = ${value}`))
          .catch((error) => console.error('Error updating task:', error));
      }
    };  

  const addTask = async (department) => {
    if (!currentUserId || !department) {
      console.error('     .');
      return;
    }

    //    
    await fetchDepartmentData(department);

    const today = new Date().toISOString().split('T')[0];

    //    
    const newTask = {
      date: today,
      status: '',
      projectName: '',
      categoriesName: '',
      subcategoriesName: '',
      subsubcategoriesName: '',
      baseTime: 0,
      spentTime: 0,
      remainingTime: 0,
      note: '',
      Uesrsid: currentUserId, //   
      userName: userData.UserName,
      userTeam: userData.UserTeam,
      department,
      availableSubcategories: [],
      availableSubSubcategories: [],
    };

    // Firestore  
    db.collection('tasks')
      .add(newTask)
      .then((docRef) => {
        //   
        setTasks(prevTasks => [...prevTasks, { id: docRef.id, ...newTask }]);
        
        //   
        return db.collection('departments')
          .doc(department)
          .get();
      })
      .then((doc) => {
        if (doc.exists) {
          setDepartmentData(prev => ({
            ...prev,
            [department]: doc.data()
          }));
        }

        //  
        setSelectedDepartment('');
        setShowDepartmentSelect(false);
      })
      .catch((error) => console.error('Error adding task:', error));
  };

  // handleDepartmentChange     
  const handleDepartmentChangeForNewTask = async (e) => {
    const department = e.target.value;
    if (department) {
      await fetchDepartmentData(department);
      addTask(department);
    }
  };

  const handleDepartmentChangeForExistingTask = async (taskId, department) => {
    if (!taskId) return;
    
    //   
    await fetchDepartmentData(department);

    // task 
    const updatedTask = {
      department,
      categoriesName: '',
      subcategoriesName: '',
      subsubcategoriesName: '',
      baseTime: 0
    };

    setTasks(prevTasks =>
      prevTasks.map(task =>
        task.id === taskId
          ? { ...task, ...updatedTask }
          : task
      )
    );

    db.collection('tasks')
      .doc(taskId)
      .update(updatedTask)
      .catch(error => console.error('Error updating task:', error));
  };

  const deleteTask = (taskId) => {
    db.collection('tasks')
      .doc(taskId)
      .delete()
      .then(() => {
        setTasks((prevTasks) => prevTasks.filter((task) => task.id !== taskId));
        console.log('Task deleted:', taskId);
      })
      .catch((error) => console.error('Error deleting task:', error));
  };

  // Calendar   
  const handleDateClick = (taskId, event) => {
    event.stopPropagation(); //   
    setSelectedTaskId(taskId);
    setIsCalendarOpen(true);
    
    const rect = event.target.getBoundingClientRect();
    setCalendarPosition({
      top: rect.bottom + window.scrollY,
      left: rect.left + window.scrollX
    });
  };

  const handleDateChange = (date) => {
    if (selectedTaskId) {
      const formattedDate = date.toISOString().split('T')[0];
      updateTaskField(selectedTaskId, 'date', formattedDate);
      setIsCalendarOpen(false);
    }
  };

  // useEffect 
  useEffect(() => {
    const handleClickOutside = (event) => {
      // calendar-container    
      const calendarContainer = document.querySelector('.calendar-container');
      
      //      ,   
      if (calendarContainer && !calendarContainer.contains(event.target) && 
          !event.target.closest('.myproject-task-row div:first-child')) {
        setIsCalendarOpen(false);
      }
    };

    //   
    document.addEventListener('mousedown', handleClickOutside);

    //      
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  //     
  const fetchDepartmentData = async (department) => {
    //        
    if (departmentData[department]) {
      return;
    }

    try {
      const doc = await db.collection('departments').doc(department).get();
      if (doc.exists) {
        setDepartmentData(prev => ({
          ...prev,
          [department]: doc.data()
        }));
      }
    } catch (error) {
      console.error(`Error fetching department data for ${department}:`, error);
    }
  };

  return (
    <div className="my-project">
      <div className="myproject-project-header">
      <div className="my-project-add-task-container">
        <button 
          onClick={() => setShowDepartmentSelect(true)} 
          className="my-project-add-task-button"
        >
          +  
        </button>
        {showDepartmentSelect && (
          <select
            value={selectedDepartment}
            onChange={handleDepartmentChangeForNewTask}
            className="department-select"
          >
            <option value=""> </option>
            {depList.map(dep => (
              <option key={dep.value} value={dep.value}>
                {dep.label}
              </option>
            ))}
          </select>
        )}
      </div>
        <div className="my-project-filters">
        <select value={filters.year} onChange={(e) => handleFilterChange('year', parseInt(e.target.value))}>
          {[2023, 2024, 2025].map((year) => (
            <option key={year} value={year}>
              {year}
            </option>
          ))}
        </select>
        <select value={filters.month} onChange={(e) => handleFilterChange('month', parseInt(e.target.value))}>
          {[...Array(12).keys()].map((month) => (
            <option key={month + 1} value={month + 1}>
              {month + 1}
            </option>
          ))}
        </select>
      </div>
      </div>
      <div className="myproject-task-table-header">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
      <div className="myproject-task-sections">
        {['', '', '', ''].map((status) => (
          <div className="myproject-task-section" key={status}>
            <h3> {status}</h3>
            {tasks
              .filter((task) => task.status === status)
              .map((task) => (
                <div className={`myproject-task-row ${task.status}`} key={task.id}>
                 <div
                  onClick={(e) => {
                    console.log('Date clicked for task:', task.id);
                    handleDateClick(task.id, e);
                  }}
                >
                  {task.date}
                </div>
                  <div>
                    <select
                      value={task.status}
                      onChange={(e) =>
                        updateTaskField(task.id, 'status', e.target.value)
                      }
                      className={`myproject-status-select ${task.status}`}
                    >
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                    </select>
                  </div>
                  <div>
                  <Select
                    options={projects}
                    value={projects.find((project) => project.value === task.projectName)}
                    onChange={(selectedOption) =>
                      updateTaskField(task.id, 'projectName', selectedOption.value)
                    }
                    placeholder=""
                    classNamePrefix="myproject-custom-select" //  
                  />
                  </div>
                  <div>
                    <select
                      value={task.categoriesName}
                      onChange={(e) =>
                        updateTaskField(task.id, 'categoriesName', e.target.value)
                      }
                      className="myproject-category-select"
                    >
                      <option value=""></option>
                      {departmentData[task.department].categories.map((category) => (
                        <option key={category.id} value={category.name}>
                          {category.name}
                        </option>
                      )) || []}
                    </select>
                  </div>
                  <div>
                    <select
                      value={task.subcategoriesName}
                      onChange={(e) =>
                        updateTaskField(
                          task.id,
                          'subcategoriesName',
                          e.target.value
                        )
                      }
                      className="myproject-category-select "
                    >
                      <option value=""></option>
                      {task.availableSubcategories.map((subcategory) => (
                        <option key={subcategory.id} value={subcategory.name}>
                          {subcategory.name}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <select
                      value={task.subsubcategoriesName}
                      onChange={(e) =>
                        updateTaskField(
                          task.id,
                          'subsubcategoriesName',
                          e.target.value
                        )
                      }
                      className="myproject-category-select "
                    >
                      <option value=""></option>
                      {task.availableSubSubcategories.map((subSubcategory) => (
                        <option
                          key={subSubcategory.id}
                          value={subSubcategory.name}
                        >
                          {subSubcategory.name}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                  {task.availableSubSubcategories
                    .filter(
                      (subSubcategory) =>
                        subSubcategory.name === task.subsubcategoriesName
                    )
                    .map((subSubcategory) => {
                      const baseTime = subSubcategory.baseTime || 0;

                      //  
                      if (task.baseTime !== baseTime) {
                        updateTaskField(task.id, 'baseTime', baseTime);
                      }

                      return (
                        <span key={subSubcategory.id}>{baseTime}</span>
                      );
                    })}
                  </div>
                  <div>
                    <input
                      type="number"
                      value={task.spentTime}
                      onChange={(e) => {
                        //    0 
                        const value = Math.max(0, parseFloat(e.target.value) || 0);
                        //    
                        const formattedValue = Math.round(value * 10) / 10;
                        updateTaskField(task.id, 'spentTime', formattedValue);
                      }}
                      step="0.1"
                      min="0"
                    />
                  </div>
                  <div>
                    <span
                      style={{
                        color: task.status === '' ? 'blue' : task.remainingTime < 0 ? 'red' : 'black',
                      }}
                    >
                      {task.status === ''
                        ? ''
                        : task.remainingTime < 0
                        ? ''
                        : (Math.round(task.remainingTime * 10) / 10).toFixed(1)}
                    </span>
                  </div>
                  <div>
                    <input
                      type="text"
                      value={task.note}
                      onChange={(e) =>
                        updateTaskField(task.id, 'note', e.target.value)
                      }
                    />
                    <button
                      onClick={() => deleteTask(task.id)}
                      className="myproject-delete-icon-button"
                    >
                      
                    </button>
                  </div>
                </div>
              ))}
          </div>
        ))}
      </div>
      {isCalendarOpen && (
        <div
          className="calendar-container"
          style={{
            position: 'absolute',
            top: calendarPosition.top,
            left: calendarPosition.left,
            zIndex: 800,
          }}
        >
          <Calendar onChange={handleDateChange} />
        </div>
      )}
    </div>
  );
}

export default MyProject;